import { Injectable } from '@angular/core';
import { ConfirmationDialogComponent } from './confirmation-dialog.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Component, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class ConfirmationDialogService {

  msgsetDeactivate: string;
  msgsetActivate: string;
  strOk:string;
  strCancel:string
  //constructor(private modalService: MatDialog) { }
  constructor(private modalService: NgbModal,
    private BLTranslate: TranslateService) {
      this.BLTranslate.get("msgsetDeactivate").subscribe(res => { this.msgsetDeactivate = res; });
      this.BLTranslate.get("msgsetActivate").subscribe(res => { this.msgsetActivate = res; });
      this.BLTranslate.get("ConfigMSGbtnOkText").subscribe(res => { this.strOk = res; });
      this.BLTranslate.get("ConfigMSGbtnCancelText").subscribe(res => { this.strCancel = res; });
    }

  public confirm(
    // title: string,
    message: string ,
    btnOkText: string = this.strOk,
    btnCancelText: string = this.strCancel,
    dialogSize: 'sm'|'lg' = 'lg'): Promise<boolean> {
    const modalRef = this.modalService.open(ConfirmationDialogComponent);
    //modalRef.componentInstance.title = title;
    modalRef.componentInstance.message = message;
    modalRef.componentInstance.btnOkText = btnOkText;
    modalRef.componentInstance.btnCancelText = btnCancelText;

    return modalRef.result;
  }

  public confirmActivation(
    // title: string,
    message: string = this.msgsetActivate,
    btnOkText: string = this.strOk,
    btnCancelText: string = this.strCancel,
    dialogSize: 'sm'|'lg' = 'lg'): Promise<boolean> {
    const modalRef = this.modalService.open(ConfirmationDialogComponent);
    //modalRef.componentInstance.title = title;
    modalRef.componentInstance.message = message;
    modalRef.componentInstance.btnOkText = btnOkText;
    modalRef.componentInstance.btnCancelText = btnCancelText;

    return modalRef.result;
  }
  
  public confirmDeactivation(
    // title: string,
    message: string = this.msgsetDeactivate,
    btnOkText: string = this.strOk,
    btnCancelText: string = this.strCancel,
    dialogSize: 'sm'|'lg' = 'lg'): Promise<boolean> {
    const modalRef = this.modalService.open(ConfirmationDialogComponent);
    //modalRef.componentInstance.title = title;
    modalRef.componentInstance.message = message;
    modalRef.componentInstance.btnOkText = btnOkText;
    modalRef.componentInstance.btnCancelText = btnCancelText;

    return modalRef.result;
  }
}
