import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { Page } from '../../../pagination/page';

//https://keepgrowing.in/angular/handle-server-side-sorting-in-an-angular-application/
@Component({
  selector: 'app-custom-pagination',
  templateUrl: './custom-pagination.component.html',
  styleUrls: ['./custom-pagination.component.scss']
})
export class CustomPaginationComponent implements OnInit {
  @Input() page: Page<any>;
  @Output() nextPageEvent = new EventEmitter();
  @Output() previousPageEvent = new EventEmitter();
  @Output() loadPageCurrentEvent = new EventEmitter();
  
  constructor() { }

  ngOnInit() {
  }

  nextPage(): void {
    this.nextPageEvent.emit(null);
  }

  previousPage(): void {
    this.previousPageEvent.emit(null);
  }
  loadPageCurrent(PageCurrent:number): void {
    //TODO::Fix this error. work fine in runtime
    this.page.pageable.pageCurrent = PageCurrent;
    this.loadPageCurrentEvent.emit(null);
  }
}
